import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import FavoriteIcon from '@material-ui/icons/Favorite';

const FVList = ({ id, AllFVPrevList, setAllFVPrevList }) => {
  const useStyles = makeStyles(() => ({
    Section: {
      position: 'absolute',
      width: '400px',
      background: '#ffffff',
      borderRadius: '10px',
    },
    FVSection: {
      height: '450px',
      top: '460px',
    },
    FVtap: {
      color: '#1976D2',
      fontWeight: 'bold',
      fontSize: 14,
    },
    PrevTab: {
      color: '#1976D2',
      fontWeight: 'bold',
      fontSize: 14,
    },

    FVList: {
      width: 320,
      marginLeft: 40,
      marginTop: -10,
      '&:hover': {
        background: '#EDF7FF;',
      },
    },
    FVIcon: {
      color: 'red',
    },
  }));

  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const [FVChecked, setFVChecked] = useState(true);

  const tapChange = (event, newValue) => {
    setValue(newValue);
    //console.log(newValue);
    if (newValue === 0) {
      setFVChecked(true);
    } else {
      setFVChecked(false);
    }
  };

  // useEffect(() => {
  //   setAllFVPrevList(AllFVPrevList);
  // }, [AllFVPrevList, setAllFVPrevList]);

  const removeFV = (id, removeName) => {
    const localFavoriteList = AllFVPrevList.map((allList) =>
      allList.id === id
        ? {
            ...allList,
            favorite: allList.favorite.filter(
              (fvName) => fvName !== removeName
            ),
          }
        : allList
    );
    //console.log("a", a);
    setAllFVPrevList(localFavoriteList);
    localStorage.setItem('FPList', JSON.stringify(localFavoriteList));
  };

  return (
    <>
      <article className={`${classes.Section} ${classes.FVSection} `}>
        <div>
          <Tabs
            //첫번째 탭일 때 value가 0, 두번째 탭일 때 value 1
            value={value}
            onChange={tapChange}
            indicatorColor="primary"
            TabIndicatorProps={{
              style: { background: '#1976D2' },
            }}
            style={{ marginTop: 20 }}
            className={classes.taps}
            centered
          >
            <Tab className={classes.FVtap} label="즐겨찾기" name="FV" />
            <Tab className={classes.PrevTab} name="prev" label="최근기록" />
          </Tabs>

          <br />

          <div className={classes.listOpen}>
            {FVChecked ? (
              <div className={classes.FVlistOpen}>
                {AllFVPrevList.map((item, index) =>
                  item.id === id
                    ? item.favorite.map((fvitem) => (
                        <ListItem key={index} button className={classes.FVList}>
                          <ListItemIcon>
                            <FavoriteIcon
                              className={classes.FVIcon}
                              onClick={(e) => {
                                removeFV(item.id, fvitem);
                              }}
                            />
                          </ListItemIcon>
                          <ListItemText
                            className={classes.searchCPList}
                            primary={fvitem}
                          />
                        </ListItem>
                      ))
                    : ''
                )}
              </div>
            ) : (
              <div></div>
            )}
            {!FVChecked ? (
              <div
                className={classes.PrevlistClose}
                // style={!FVChecked ? { display: "inline" } : { display: "none" }}
              >
                {AllFVPrevList.map((item) =>
                  item.id === id
                    ? item.prevList.map((previtem, index) => (
                        <ListItem key={index} button className={classes.FVList}>
                          <ListItemText
                            className={classes.searchCPList}
                            primary={previtem}
                          />
                        </ListItem>
                      ))
                    : ''
                )}
              </div>
            ) : (
              <div></div>
            )}
          </div>
        </div>
      </article>
    </>
  );
};

export default FVList;
