import React, { useEffect, useState } from 'react';
import '.././App.css';
import { makeStyles } from '@material-ui/core/styles';
import Header from '../component/Header';
import Login from '../component/Login';
import FVList from '../component/FVList';
import SearchList from '../component/SearchList';
import axios from 'axios';
import Button from '@material-ui/core/Button';
const Main = () => {
  const useStyles = makeStyles((theme) => ({
    App: {
      margin: '0 auto',
      textAlign: 'center',
      fontFamily: 'Noto Sans KR',
      fontStyle: 'normal',
      fontWeight: 'normal',
      color: '#333333',
      background: '#e5e5e5',
      height: '100vh',
    },
    section: {
      width: '1000px',
      margin: '0 auto',
    },

    FVList: {
      width: 320,
      marginLeft: 40,
      marginTop: -10,
      '&:hover': {
        background: '#EDF7FF;',
      },
    },
    FVIcon: {
      color: 'red',
    },
  }));
  // const [list, setList] = useState(false);
  const [id, setID] = useState('');
  const [AllFVPrevList, setAllFVPrevList] = useState([
    // { localstorage form
    //   id: "rlatjsgml",
    //   favorite: ["(주)월급날", "카카오", "네이버", "야후"],
    //   prevList: ["현대자동차", "한미제약", "네이버", "Apple", "삼성전자"],
    // },
    // {
    //   id: "sunny1",
    //   favorite: ["(주)월급날", "아시아나", "티웨이항공", "코카콜라"],
    //   prevList: [
    //     "삼성전자",
    //     "진에어",
    //     "Apple",
    //     "야후",
    //     "엘지유플러스",
    //     "대한항공",
    //   ],
    // },
  ]);
  const classes = useStyles();
  const [AllList, setAllList] = useState([]);

  useEffect(() => {
    //db에서 데이터 받아온 검색리스트
    axios.get('http://localhost:3000/data').then((response) => {
      //CompanyList에 서버명, 회사명 저장
      setAllList(response.data);
    });
  }, []);

  const onLogin = (id) => {
    setID(id);
    var getFPList = JSON.parse(localStorage.getItem('FPList'));
    if (getFPList !== null) {
      setAllFVPrevList(JSON.parse(localStorage.getItem('FPList')));
    } else {
      setAllFVPrevList([
        //로컬스토리지에 데이터가 없으면 id, favorite, prevList 가 들어있는 객체 만들어서 넣기
        {
          id: '',
          favorite: [],
          prevList: [],
        },
      ]);
    }
  };

  return (
    <>
      <div className={classes.App}>
        <Header />

        <section className={classes.section}>
          <Login onLogin={onLogin} /* id={id} setId={setID}*/ />

          <FVList
            id={id}
            AllFVPrevList={AllFVPrevList}
            setAllFVPrevList={setAllFVPrevList}
          />
          <SearchList
            AllList={AllList}
            id={id}
            AllFVPrevList={AllFVPrevList}
            setAllFVPrevList={setAllFVPrevList}
          />
        </section>
      </div>
    </>
  );
};
export default Main;
