import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

import Autocomplete from '@material-ui/lab/Autocomplete';
import SearchListitem from './SearchListitem';

const SearchList = ({ AllList, id, AllFVPrevList, setAllFVPrevList }) => {
  const useStyles = makeStyles(() => ({
    Section: {
      position: 'absolute',
      width: '400px',
      background: '#ffffff',
      borderRadius: '10px',
    },
    searchSection: {
      width: '580px',
      height: '820',
      top: '90px',
      marginLeft: '430px',
    },
    searchCompany: {
      marginTop: 55,
      width: 480,
      height: '56px',
      marginLeft: '50px',
      '& label': {
        fontSize: '14px',
        fontWeight: 'bold',
        lineHeight: '20px',
      },
      '& TextField': {
        fontSize: '16px',
        lineHeight: '24px',
        letterSpacing: '0.01em',
        color: '#263238',
        display: 'flex',
      },
      '& .MuiOutlinedInput-root': {
        height: '56px',
      },
    },
    searchIcon: {
      color: 'red',
    },
    searchIcon2: {
      color: '#CCCCCC',
    },
    searchList: {
      width: 480,
      marginLeft: 50,
      '&:hover': {
        background: '#EDF7FF;',
      },
    },
    searchCPList: {
      color: '#333333',
      lineHeight: 20,
      letterSpacing: -0.01,
      '& span': {
        fontSize: '14px',
      },
    },
  }));

  const classes = useStyles();
  const [searchList, setSearchList] = useState([]);

  //처음 화면 켜졌을 때 모든 리스트 가져오기
  useEffect(() => {
    setSearchList(AllList);
  }, [AllList]);

  //입력한 text랑 이름이 같은 리스트만 보이기
  const search = (val) => {
    var test2 = val;
    console.log('test2', test2);
    console.log('val', val);
    // setSearchList(
    //   AllList.filter((item) => item.title === val || item.server === val)
    // );
    var test = AllList.filter(
      (item) => item.title === test2 || item.server === test2
    );
    setSearchList(test);
  };

  return (
    <>
      <article className={`${classes.Section} ${classes.searchSection} `}>
        <div>
          <Autocomplete
            freeSolo
            id="combo-box-demo"
            options={AllList}
            getOptionLabel={(option) => option.title}
            className={classes.searchCompany}
            renderInput={(params) => (
              <TextField
                {...params}
                label="회사검색"
                variant="outlined"
                //value={searchInput}
                onSelect={(e) => {
                  //검색한 텍스트 저장
                  //var title = e.target.value;
                  //console.log('title', title);
                  search(e.target.value);
                  //아무것도 검색안했을 때 모든 리스트 보이기
                  if (e.target.value === '') {
                    setSearchList(AllList);
                  }
                }}
              />
            )}
          />
          <br />
          <SearchListitem
            AllList={searchList}
            setAllFVPrevList={setAllFVPrevList}
            AllFVPrevList={AllFVPrevList}
            id={id}
          />
        </div>
      </article>
    </>
  );
};

export default SearchList;
