//import reactDom from "react-dom";
import { makeStyles } from '@material-ui/core/styles';
import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import FavoriteIcon from '@material-ui/icons/Favorite';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
//import axios from "axios";
import produce from 'immer';
const SearchListitem = ({ AllList, id, AllFVPrevList, setAllFVPrevList }) => {
  const useStyles = makeStyles((theme) => ({
    searchListArea: {
      overflowY: 'auto',
      height: '680px',
    },
    searchIcon: {
      color: 'red',
    },
    searchIcon2: {
      color: '#CCCCCC',
    },
    searchList: {
      width: 480,
      marginLeft: 50,
      '&:hover': {
        background: '#EDF7FF;',
      },
    },
    searchCPList: {
      color: '#333333',
      lineHeight: 20,
      letterSpacing: -0.01,
      '& span': {
        fontSize: '14px',
      },
    },
  }));
  //var getFPList = JSON.parse(localStorage.getItem("FPList"));
  const classes = useStyles();

  const setPrevList = (data) => {
    const setFPList = produce(AllFVPrevList, (draft) => {
      //조회한 아이디랑 일치하는 즐겨찾기 리스트를 list에 넣기
      var list = draft.find((item) => item.id === id);
      //아이디가 있으면
      if (list !== undefined) {
        //같은 데이터가 들어있으면 그 데이터 값 지워주기
        if (list.prevList.findIndex((item) => item === data) !== -1) {
          list.prevList.splice(
            list.prevList.findIndex((item) => item === data),
            1
          );
          //즐겨찾기 리스트가 9개면 맨처음꺼 지우기
        } else if (list.prevList.length === 9) {
          list.prevList.splice(0, 1);
        }
        //데이터 넣기
        list.prevList = list.prevList.concat(data);
      } else {
        //아이디가 없으면 새로만들어주기
        draft.push({
          id: id,
          favorite: [],
          prevList: [data],
        });
      }
    });
    localStorage.setItem('FPList', JSON.stringify(setFPList));
    setAllFVPrevList(setFPList);
  };

  const setFVList = (data) => {
    const setFPList = produce(AllFVPrevList, (draft) => {
      //조회한 아이디랑 일치하는 즐겨찾기 리스트를 list에 넣기
      var list = draft.find((item) => item.id === id);
      if (list !== undefined) {
        //즐겨찾기에 일치하는 값이 없으면
        list.favorite.findIndex((item) => item === data) === -1
          ? //즐겨찾기 항목에 추가
            (list.favorite = list.favorite.concat(data))
          : //일치하면 항목에서 제외
            list.favorite.splice(
              list.favorite.findIndex((item) => item === data),
              1
            );
        //즐겨찾기 리스트가 10개면 맨처음꺼 지우기 9개까지만 표시
        if (list.favorite.length === 10) {
          list.favorite.splice(0, 1);
        }
      } else {
        //해당 id의 즐겨찾기 리스트가 없으면 새로 만들기
        draft.push({
          id: id,
          favorite: [data],
          prevList: [],
        });
      }
    });
    //로컬 스토리지에 저장
    localStorage.setItem('FPList', JSON.stringify(setFPList));

    //즐겨찾기 리스트에 저장
    setAllFVPrevList(setFPList);
  };

  return (
    <div className={classes.searchListArea}>
      {AllList.map((item, index) => {
        //검색리스트랑 즐겨찾기 리스트중 id 같은 것만 temp에 담기
        const temp = AllFVPrevList.find((item) => {
          return item.id === id;
        });
        //temp가 없으면 false (빈하트)
        var heart = false;
        if (temp !== undefined) {
          //즐겨찾기 리스트랑 검색리스트랑 이름같으면 true
          heart = temp.favorite.find((item3) => item3 === item.title)
            ? true
            : false;
        }
        return (
          <ListItem key={index + item} button className={classes.searchList}>
            <ListItemIcon
              onClick={(e) => {
                setFVList(item.title);
              }}
            >
              {heart ? (
                <FavoriteIcon className={classes.searchIcon} />
              ) : (
                <FavoriteBorderIcon className={classes.searchIcon2} />
              )}
            </ListItemIcon>
            <ListItemText
              className={classes.searchCPList}
              primary={`${item.title} - ${item.server} `}
              onClick={() => {
                setPrevList(item.title);
              }}
            />
          </ListItem>
        );
      })}
    </div>
  );
};

export default SearchListitem;
